const sectionLinks: SectionInterface[] = [
  {
    name: "About Me",
    icon: AboutIcon,
  },
  {
    name: "Projects",
    icon: ProjectIcon,
    shorthand: "projects",
  },
  {
    name: "Skills",
    icon: SkillIcon,
    shorthand: "skills",
  },
  {
    name: "Testimonials",
    icon: TestimonialIcon,
    shorthand: "testimonials",
  },
  {
    name: "Contact Me",
    icon: ContactIcon,
    shorthand: "contact",
  },
];

export const useSections = (data: any | object, admin: boolean) => {
  const filteredLinks = sectionLinks.filter((el) => {
    if (el.shorthand) {
      if (data[el.shorthand].length) return true;
      else return false;
    } else return true;
  });
  return filteredLinks;
};

export default useSections;
