import React, { useState } from "react";
import "./sidebar.css";
import Linke from "./Link";
import useSections from "./SidebarSections";
import { Link } from "react-router-dom";
import { useData } from "../../context/DataContext";
const Sidebar = ({ admin = false }) => {
  const [expand, setExpand] = useState(false);
  const data = useData();
  const sections = useSections(data, admin);
  const showLinks = () =>
    sections.map((el, i) =>
      admin ? (
        <Link
          key={i}
          to={`/panel/add/${el.name.toLowerCase().split(" ").join("_")}`}
        >
          <Linke {...el} expand={expand} />
        </Link>
      ) : (
        <div key={i}>
          <Linke {...el} expand={expand} />
        </div>
      )
    );
  const toggleExpand = () => {
    setExpand((e) => !e);
    document.body.classList.toggle("hide_site");
  };
  const removeExpand = () => {
    setExpand(false);
    document.body.classList.remove("hide_site");
  };
  return (
    <div
      className={expand ? "sidebar_outer" : ""}
      onClick={() => (expand ? removeExpand() : "")}
    >
      <div className={`sidebar ${expand ? "sidebar_expand" : ""}`}>
        <div className="menu">
          <div className="upr">{showLinks()}</div>
          <div className="btm">
            <Linke
              expandBtn={true}
              icon={() => <span className="back_icon"> {">"} </span>}
              toggleExpand={toggleExpand}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
