import React, { useRef, useEffect } from "react";
import "./header.css";
import { Link } from "react-router-dom";
import Icon from "../../utils/Icon";
import { useCursor } from "../../context/useCursor";
const Header = ({ admin = false }) => {
  const header = useRef<HTMLDivElement>(document.createElement("div"));
  useEffect(() => {
    if (!admin) {
      window.addEventListener("scroll", (e) => {
        const { scrollY, innerHeight } = window;
        const { current } = header;
        if (current) {
          if (scrollY > innerHeight - current.offsetHeight) {
            current.classList.add("scroll_header");
          }

          if (scrollY < innerHeight - current.offsetHeight) {
            current.classList.remove("scroll_header");
          }
        }
      });
    }
  }, [admin]);
  return (
    <div ref={header} className={`my_header ${admin ? "scroll_header" : ""}`}>
      <Link to="/" className="header_content">
        <div className="header_icon">
          <Icon />
        </div>
        <div className="header_name">
          <div className="heading">
            <span className="buttonD">A</span>
            {"deeb Ahmad".split("").map((el, i) => (
              <i key={i} className="buttonD">
                {el}
              </i>
            ))}
          </div>
          <div className="sub_heading">
            <span className="buttonD">A</span>
            {" Web Developer".split("").map((el, i) => (
              <i key={i} className="buttonD">
                {el}
              </i>
            ))}
          </div>
        </div>
      </Link>
    </div>
  );
};

export default Header;
