import React, { FC, useRef } from "react";
import { useCursor } from "../../context/useCursor";

interface Props {
  icon: FC;
  name?: string;
  expand?: boolean;
  toggleExpand?: Function;
  expandBtn?: boolean;
  shorthand?: string;
}

const Link: FC<Props> = ({
  icon: Icon,
  name,
  expand,
  toggleExpand,
  expandBtn,
}) => {
  const fancy = useRef<HTMLDivElement>(document.createElement("div"));
  const { handleMouseEnter, handleMouseLeave } = useCursor();
  const scrollToSection = () => {
    if (expandBtn) return;
    if (!name) return;
    const section: HTMLElement | null = document.querySelector(
      `[data-name=${name.toLowerCase().split(" ").join("_")}]`
    );
    if (!section) return;
    window.scrollTo({ behavior: "smooth", top: section.offsetTop - 100 });
  };
  return (
    <div
      ref={fancy}
      onPointerEnter={handleMouseEnter}
      onPointerLeave={handleMouseLeave}
      className="fancy_outer"
      onClick={(e) => {
        scrollToSection();
        if (toggleExpand) {
          toggleExpand();
        }
        setTimeout(() => {
          if (fancy.current || e) {
            handleMouseEnter(
              {
                currentTarget: fancy.current || document.createElement("div"),
              } || e
            );
          }
        }, 400);
      }}
    >
      <div
        className="fancy-hover"
        onPointerMove={(e) => {
          if (fancy.current) {
            fancy.current.style.setProperty(
              "--px",
              (e.clientX - fancy.current.offsetLeft).toString()
            );
            fancy.current.style.setProperty(
              "--py",
              (e.clientY - fancy.current.offsetTop).toString()
            );
          }
        }}
      >
        <div className="menu_icon">
          <Icon />
        </div>
        <span className="icon_name_inner"> {name} </span>
      </div>
      {name ? !expand ? <span className="icon_name">{name}</span> : "" : ""}
    </div>
  );
};

export default Link;
